html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "Arimo-Regular", georgia, sans-serif;
  font-size: 20px; }
  @media (max-width: 767px) {
    body {
      font-size: 18px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

svg path {
  fill: inherit; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

@font-face {
  font-family: "DidactGothic-Regular";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("DidactGothic-Regular"), url("../fonts/DidactGothic-Regular.woff2") format("woff2"), url("../fonts/DidactGothic-Regular.woff") format("woff"); }

@font-face {
  font-family: "Arimo-Regular";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("Arimo-Regular"), url("../fonts/Arimo-Regular.woff2") format("woff2"), url("../fonts/Arimo-Regular.woff") format("woff"); }

.btn {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer; }

.container {
  max-width: 1216px;
  padding: 0 45px;
  margin: 0 auto; }
  @media (max-width: 1246px) {
    .container {
      padding: 0 15px; } }

.title {
  font-family: "DidactGothic-Regular", calibri, sans-serif; }

.title--first {
  font-family: "DidactGothic-Regular", calibri, sans-serif;
  font-size: 48px; }
  @media (max-width: 767px) {
    .title--first {
      font-size: 32px; } }

.header {
  padding: 30px 0; }

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  @media (max-width: 1023px) {
    .header__container {
      justify-content: space-around; } }
  @media (max-width: 767px) {
    .header__container {
      flex-direction: column; } }

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  @media (max-width: 767px) {
    .header__wrapper {
      width: 100%;
      margin-bottom: 10px; } }

.header__logo {
  display: none; }
  .header__logo--active {
    display: block;
    width: 70px;
    height: 70px; }
    @media (max-width: 767px) {
      .header__logo--active {
        width: 40px;
        height: 40px; } }

@media (max-width: 767px) {
  .header__nav {
    display: none; } }

.header__nav--opened {
  display: block; }

.header__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex; }
  @media (max-width: 767px) {
    .header__list {
      flex-direction: column; } }

.header__item {
  margin-right: 30px; }
  .header__item:last-child {
    margin-right: 0; }

.header__link {
  color: #808080;
  transition: color 0.3s ease-in; }
  .header__link:hover, .header__link:focus {
    text-decoration: none;
    color: #000000; }
  .header__link--active {
    color: #000000; }

.header__toggle {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  display: none;
  margin-left: auto;
  background: url("../img/icons/icon-menu.svg") center no-repeat transparent;
  background-size: contain; }
  .header__toggle--opened {
    background-image: url("../img/icons/icon-croix.svg"); }
  @media (max-width: 767px) {
    .header__toggle {
      display: block;
      width: 20px;
      height: 20px; } }

.work h1 {
  margin: 0 0 20px 0; }

.work p {
  margin: 0; }
